
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import XProgress from 'vux/src/components/x-progress/index.vue'

export default {
components: {
Tab,
TabItem,
XProgress
},
data() {
return {
initItem1: '品牌一部',
scroll: '',
index: 0,
isfixed: false,
tabOffsetTop: 10,
ware: [
{
name: '品牌一部',
total: 544887878,
canSall: 6898779
},
{
name: '品牌二部',
total: 87687878,
canSall: 682398779
},
{
name: '品牌三部',
total: 21548414887878,
canSall: 3994149
},
{
name: '品牌四部',
total: 2654948,
canSall: 1575945
},
{
name: '品牌五部',
total: 354491484,
canSall: 6892499997
}
]
}
},
mounted() {
window.addEventListener('scroll', this.scrolling);
this.tabOffsetTop = this.$refs.tabbox.offsetTop + 40;
},
created: function() {
},
methods: {
clickHandler: function(index) {
let targetoffTop = this.$refs.listbox.children[index].offsetTop;
index >= 2 ? document.documentElement.scrollTop = targetoffTop - 86 : targetoffTop = 0;
index >= 2 ? this.isfixed = true : this.isfixed = false
},
scrolling: function() {
this.scroll = document.documentElement.scrollTop || document.body.scrollTop;
if (this.scroll > this.tabOffsetTop) {
this.isfixed = true;
} else {
this.isfixed = false;
}
}
}
};



