
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import XProgress from 'vux/src/components/x-progress/index.vue'

export default {
components: {
Tab,
TabItem,
XProgress
},
data() {
return {
initItem: '品牌一部',
scroll: '',
index: 0,
isfixed: false,
tabOffsetTop1: 10,
age: [
{
name: '品牌一部',
average: 325,
date30: 98797,
date90: 154878,
date150: 24848
},
{
name: '品牌二部',
average: 264,
date30: 1545,
date90: 1423,
date150: 987
},
{
name: '品牌三部',
average: 200,
date30: 1544,
date90: 36477,
date150: 265654
},
{
name: '品牌四部',
average: 168,
date30: 26596549,
date90: 546544,
date150: 36549494
},
{
name: '品牌五部',
average: 92,
date30: 6549,
date90: 5848,
date150: 3654
}
]
}
},
mounted() {
window.addEventListener('scroll', this.scrolling);
this.tabOffsetTop1 = 272;
},
created: function() {
},
methods: {
clickHandler: function(index) {
let targetoffTop = this.$refs.listbox.children[index].offsetTop;
index >= 2 ? document.documentElement.scrollTop = targetoffTop - 86 : targetoffTop = 0;
index >= 2 ? this.isfixed = true : this.isfixed = false
},
scrolling: function() {
this.scroll = document.documentElement.scrollTop || document.body.scrollTop;
if (this.scroll > this.tabOffsetTop1) {
this.isfixed = true;
} else {
this.isfixed = false;
}
}
}
};



